.learn {
  &__title {
    margin-bottom: 45px;
  }

  &__list {
    @include flex-all-sb;
    gap: 70px;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-top: 50px;
    padding-bottom: 60px;
    padding-left: 45px;
    padding-right: 45px;
    width: 100%;
    background: #323232;
    height: 500px;
    border-radius: 25px;
    img {
      position: absolute;
      left: 50%;
      top: 60%;
      transform: translate(-50%, -50%);
    }
  }

  &__text {
    max-width: 325px;
  }

  &__item-title {
    font-size: 24px;
    font-weight: 400;
    line-height: 29.05px;
  }

  &__item-descr {
  }

  &__btn {
    margin-top: auto;
    padding: 15px 115px;
  }
}
.container {
}
.section {
  &__title {
  }
}
.list-reset {
}
.text {
}
.image {
}
.btn {
}


@media (max-width: "1600px" ) {
  .learn__list {
    flex-wrap: wrap;
  }
  .learn__item-title {
    text-align: center;
  }
  .learn__item-descr {
    text-align: center;
  }
  .learn__text {
    margin: 0px auto;
  }
}

@media (max-width: "480px" ) {
  .learn__btn {
    padding: 15px 0;
  }
}

@media (max-width: "378px" ) {
  .learn img {
    transform: scale(0.6);
    top: 49%;
    left: 6%;
  }
}