.galery .swiper .swiper-button-prev,
.galery .swiper .swiper-button-next {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}

.galery .swiper .swiper-button-prev {
  left: 15%;
  width: 40px;
  height: 40px;
}

.swiper .swiper-button-next::after,
.swiper .swiper-button-prev::after {
  content: "";
}

.swiper .swiper-button-next:hover svg circle,
.swiper .swiper-button-prev:hover svg circle {
  fill: white;
}

.swiper .swiper-button-next:hover svg circle,
.swiper .swiper-button-prev:hover svg circle {
  fill: white;
}

.swiper .swiper-button-next:hover svg path,
.swiper .swiper-button-prev:hover svg path {
  fill: black;
}

.swiper .swiper-button-next svg path,
.swiper .swiper-button-prev svg path,
.swiper .swiper-button-next svg circle,
.swiper .swiper-button-prev svg circle {
  transition: all 0.3s ease-in-out;
}

.galery .swiper .swiper-button-next {
  left: 85%;
  width: 40px;
  height: 40px;
}

.galery .swiper-slide a img {
  margin: 0px auto;
  width: 100%;
  object-fit: cover;
}

.galery .swiper-slide a {
  max-width: 900px;
  margin: 0px auto;
  display: flex;
  width: 100%;
  justify-content: center;
}
