.footer {
    padding: 10px 0;
  background: white;
  border-top: 3px solid var(--blue);
  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
  }

  &__dev-link {
    display: flex;
    gap: 10px;
    align-items: center;
    color: black;
    font-family: var(--fontFamily);
    font-size: 18px;
    font-weight: 400;
    line-height: 1;
    text-align: left;
    & svg path {
      transition: 0.3s all ease-in-out;
    }
    &:hover svg path:first-child {
      fill: var(--blue);
    }
    &:hover {
        text-decoration: underline;
    }
  }

  &__privacy-policy {
    color: black;
        font-family: var(--fontFamily);
font-size: 18px;
font-weight: 400;
line-height: 1;
text-align: left;
&:hover {
        text-decoration: underline;
    }

  }
}
.container {
}


@media (max-width: "780px") {
    .footer__logo {
      width: 150px;
    }
}


@media (max-width: "666px") {
    .footer__logo {
      display: none;
    }
}

@media (max-width: "520px") {
    .footer__privacy-policy {
      font-size: 14px;
    }
    .footer__dev-link {
      font-size: 14px;
    }
}