@media (max-width: "1400px") {
  #contacts iframe {
    width: 50%;
  }

}
@media (max-width: "1000px") {
.contacts__container {
  display: flex;
  flex-direction: column;
}
#contacts iframe {
  width: 100%;
}
.contacts__content {
  margin: 0px auto;

}
.contacts__list {
  margin-bottom: 0;
}
.socials__menu {
  margin-bottom: 40px;
}
.contacts__title {
  text-align: center;
  margin-bottom: 20px;
}
 }


