.form {
    
}

.form__container {
    display: flex;
	justify-content: space-between;
	img {
		width: 50%;
		border-radius: 25px;
	}
	iframe {
		width: 100%;
		border-radius: 30px;
		height: 697px ;
	}
}
.form {

		&__container {
		}

		&__wrapper {
		}

		&__title {

		}
}


.btn {
	padding-top: 26px;
	padding-bottom: 26px;
}


#form {
    display: flex;
    flex-direction: column;
	label {
		font-family: var(--fontFamily);
font-size: 32px;
font-weight: 400;
line-height: 38.73px;
text-align: left;
color: #fff;

	}
	input, textarea {
		margin-bottom: 25px;
		border-radius: 28px;
		padding: 17px 30px 17px;
		width: 100%;

	}
	#form-descr {
		min-height: 100px;
		resize: none;
		margin-bottom: 50px;
	}
	
}


@media (max-width: "1050px") {
.form__container {
flex-direction: column;
}
.form img {
	width: 60%;
	margin: 0px auto;
	margin-top: 50px;
}
#form #form-descr {
	margin-bottom: 20px;
}
#form input, #form textarea {
		margin-bottom: 10px;
}

}