.documents .swiper .swiper-button-prev,
 .documents .swiper .swiper-button-next{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
}



.documents .swiper .swiper-button-prev {
    left: 50px;
    width: 40px;
    height: 40px;
}

.documents .swiper .swiper-button-next {
    left: 95%;
    width: 40px;
    height: 40px;
}


@media (max-width: "710px") {
    .documents .swiper .swiper-button-next {
        left: 93%;
    }
    .documents .swiper-button-prev {
        
    }
}

@media (max-width: "615px") {
    .documents .swiper .swiper-button-next {
        
    }
    .documents .swiper .swiper-button-prev {
        left: 40px;
    }
}


@media (max-width: "480px") {
    .documents .swiper .swiper-button-next {
        left: 90%;
    }
    .documents .swiper .swiper-button-prev {
        left: 30px;
    }
}