// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Open Sans", sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --dark: #1e1e1e;
  --darkText: #000;
  --blue: #00CCFF;
  --blueHover: #50C3E0;

  //fonts
  --fontFamily: 'Inter';

}
