.mobile-container {
  display: none;
}

.mobile-container {
  position: absolute;
  width: 100%;
  margin-top: -40px;
  display: none;
  .nav {
    &__list {
      display: flex;
      flex-direction: column;
    }
    &__item {
      width: 100%;
    }
    &__link {
      color: black;
      border-color: black;
      background: var(--light-color);
      width: 100%;
    }
  }
}

.mobile-container-active {
  display: block;
}

.image {
}
.nav {
