}
.list-reset {
}

.burger-checkbox {
  position: absolute;
  visibility: hidden;
}
.burger {

  cursor: pointer;
  display: none;
  position: relative;
  border: none;
  background: transparent;
  width: 40px;
  height: 26px;
  // margin: 30px auto;

}

#burger-checkbox {
  margin-left: auto;
}

.burger::before,
.burger::after {
  content: '';
  left: 0;
  position: absolute;
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 10px;
  background: #000;
}
.burger::before {
  top: 0;
  box-shadow: 0 11px 0 #000;
  transition: box-shadow .3s .15s, top .3s .15s, transform .3s;
}
.burger::after {
  bottom: 0;
  transition: bottom .3s .15s, transform .3s;
}
.burger-checkbox:checked + .burger::before {
  top: 11px;
  transform: rotate(45deg);
  box-shadow: 0 6px 0 rgba(0,0,0,0);
  transition: box-shadow .15s, top .3s, transform .3s .15s;
}
.burger-checkbox:checked + .burger::after {
  bottom: 11px;
  transform: rotate(-45deg);
  transition: bottom .3s, transform .3s .15s;
}



@media (max-width: "810px") {
  .header__list {
    gap: 50px;
  }
}

@media (max-width: "769px") {
  .header__item:nth-child(2) {
    display: none;
  }
  .burger {
    display: block;
  }

  .header__bottom-container {
    display: none;
  }
  .mobile-container-active {
    margin-top: 0;
  }
}


@media (max-width: "543px") {
.header__logo {
  width: 130px;
}
.header__link svg {
  display: none;
}
.header__link span {
  font-size: 18px;
}
}